        sortable: false
      },
      {
        key: 'endYear',
        label: 'End Year',
        sortable: false
      },
      {
        key: 'acceptStatus',
        label: 'Status',
        sortable: false
      },
      {
        key: 'action_download',
        label: 'Download',
      },
      {
        key: 'action_accept',
        label: this.$t('accept'),
      },
      {
        key: 'action_reject',
        label: this.$t('reject'),
      },
          ],
    r_transcript_fields: [
      {
        key: 'createdAt',
        label: 'CreatedAt'
      },
      {
