    },
  }
},
mounted()
{
  this.checkPermission();
  this.show();
}
,
methods: {
  checkPermission()
  {
    this.$axios.post("permisson/hasPermission" , {permission: "admin"}).then((res)=>{
      if(res.data.permission === false)
      {
        this.$router.push('/')
      }
    }).catch((err) => {
      this.$router.push('/')
    })
  },
  makeToast(variant = 'success', header = 'Message', msg = '') {
    this.$bvToast.toast(this.$t(msg), {
      title: header,
      variant: variant,
      solid: true
    })
  },

  show(index) {

    if(index > 0)
      this.searchData.pageIndex = index
    else
      this.searchData.pageIndex = 0

    this.overlay_show = true;
    this.$axios.post('/users/changes', this.searchData).then((res) => {
      this.t_items = res.data.rows
      this.searchData.count = res.data.count
      this.overlay_show = false;
    }).catch(error => {
      this.overlay_show = false;
      this.makeToast("danger", "Error", "search_error")
      console.log(error)
    });
  }
  ,
  showEditModal(item)
  {
    this.userId = item.item.id
    this.certItems = item.item.certItems;
    this.transcriptItem = item.item.transcriptItem;
    this.eduItems = item.item.eduItems;
    this.reviewItem = item.item;
    this.lessonItems = item.item.lessonItems;

    this.reviewMode = true
  },

  accept(type, id )
  {
    this.$bvModal.msgBoxConfirm(this.$t('accept_confim'), {
      title: 'Please Confirm',
      size: 'sm',
      buttonSize: 'sm',
      okVariant: 'danger',
      okTitle: 'YES',
      cancelTitle: 'NO',
      headerClass: 'p-2 border-bottom-0',
      footerClass: 'p-2 border-top-0',
      hideHeaderClose: false,
      centered: true
    })
      .then(value => {
        if(value) {
          const ACCEPTED = 1;
          let changeStatus = {type: type, id: id, status: ACCEPTED, userId: this.userId}
          this.$axios.post('/users/changeStatus', changeStatus).then((res) => {
            if (res.data === "success") {

              if(type === "cert")
              {
                this.certItems.forEach(function(item, index, object){
                  if(item.id === id)
                    item.acceptStatus = 1
                    //object.splice(index, 1);
                })
              }

              if(type === "educ")
              {
                this.eduItems.forEach(function(item, index, object){
                  if(item.id === id)
                    item.acceptStatus = 1
                })
              }

              if(type === "lesson")
              {
                this.lessonItems.forEach(function(item, index, object){
                  if(item.id === id)
                    item.acceptStatus = 1
                })
              }

              if(type === "user")
              {
                this.reviewItem.acceptStatus = 1
              }

              if(type === "transcript")
              {
                this.transcriptItem.acceptStatus = 1
              }

            }
          }).catch(error => {
            this.overlay_show = false;
            this.makeToast("danger", "Error", "search_error")
            console.log(error)
          });
        }
      })
      .catch(err => {
        // An error occurred
      })

  }
  ,
  reject(type, id)
  {
    const REJECT = 2;
    this.$bvModal.msgBoxConfirm(this.$t('accept_confim'), {
      title: 'Please Confirm',
      size: 'sm',
      buttonSize: 'sm',
      okVariant: 'danger',
      okTitle: 'YES',
      cancelTitle: 'NO',
      headerClass: 'p-2 border-bottom-0',
      footerClass: 'p-2 border-top-0',
      hideHeaderClose: false,
      centered: true
    })
      .then(value => {
        if(value) {
          const ACCEPTED = 1;
          let changeStatus = {type: type, id: id, status: REJECT, userId: this.userId}
          this.$axios.post('/users/changeStatus', changeStatus).then((res) => {
            if (res.data === "success") {

              if(type === "cert")
              {
                this.certItems.forEach(function(item, index, object){
                  if(item.id === id)
                    item.acceptStatus = 2
                })
              }

              if(type === "educ")
              {
                this.eduItems.forEach(function(item, index, object){
                  if(item.id === id)
                    item.acceptStatus = 2
                })
              }

              if(type === "lesson")
              {
                this.lessonItems.forEach(function(item, index, object){
                  if(item.id === id)
                    item.acceptStatus = 2
                })
              }

            }
          }).catch(error => {
            this.overlay_show = false;
            this.makeToast("danger", "Error", "search_error")
            console.log(error)
          });
        }
      })
      .catch(err => {
        // An error occurred
      })


  }
  ,
  sendMessage(msg)
  {

  },

  downloadFile(rowId) {
    try {
      var c = {responseType: 'blob'}

      this.$axios.get(`/download/${rowId}` ,  c).then((response) => {
        console.log(response)
        var fileURL = window.URL.createObjectURL(new Blob([response.data],{type: 'application/file'}));
        var fileLink = document.createElement('a');
        var filename = response.headers['filename']

        fileLink.href = fileURL;
        fileLink.setAttribute('download', filename);
        document.body.appendChild(fileLink);

        fileLink.click();
      });

    } catch (e) {
      console.log(e);
    }
  },

  backBtn()
  {
    this.$nuxt.$emit('initAdmin')

    this.reviewMode = false
  }

}

}
